* button,* .footer{color:#753e5c}* a{color:#dd2e44;text-decoration:none}body{font-family:'Balsamiq Sans', cursive;font-weight:bold;display:flex;margin:0px;flex-direction:column;align-items:center;-webkit-touch-callout:none;-webkit-user-select:none;-khtml-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none}body button{font-family:'Fredoka One', cursive;font-weight:normal}.footer{height:60px;display:flex;flex-direction:column;align-items:center;justify-content:flex-end;font-family:'Balsamiq Sans', cursive;font-weight:bold;text-align:center}.footer p{width:100%;margin:0}#container{margin-top:30px;margin-bottom:30px;min-height:calc(100vh - 120px);max-width:860px;justify-content:space-between;display:flex;flex-direction:row;width:100%}#container #left{display:flex;flex-direction:column;align-items:center;margin-bottom:20px}#container #left #site-marker{font-family:'Fredoka One', cursive;font-weight:normal;position:relative;top:5px;left:5px;width:100%;height:0;color:#753e5caa;-moz-user-select:none;-webkit-user-select:none;user-select:none;pointer-events:none;font-size:20px}#container #left canvas{width:400px;height:500px;border-radius:4px;box-shadow:pink 0 0 0 1px}#container #left #buttons{margin-top:8px;display:grid;grid-column-gap:6px;grid-template-columns:auto auto}#container #options{display:inline-flex;flex-direction:column;vertical-align:top;height:100%;width:442px;min-width:320px;color:#753e5c}#container #options h3{margin:0}#container #options .page{padding:20px;background-color:lavenderblush;border:solid 1px pink;border-radius:0 6px 6px 0;margin-top:-1px;display:none}#container #options .page.selected{display:block}@media only screen and (max-width: 900px){#container{flex-direction:column;align-items:center}#container #options{width:100%;max-width:400px;padding:0}.footer{font-size:16px;width:auto;padding:0 10px}}@media only screen and (max-width: 400px){#container #left{max-width:375px}#container #left canvas{max-width:375px;max-height:469px}#container #options{max-width:375px}.footer{font-weight:normal}}td{vertical-align:middle;height:100%;text-align:right}td>div{display:contents}
